export default {
  name: "qrcode",
  data() {
    return {
      pay_order: undefined,
      created_qr: false,
      try_count:0
    };
  },
  created() {
    //this.$app.header(false);
    //this.$app.footer(false);

    window._t = this;
    var pay_order_id = this.$route.params.id;
    this.load_pay_order(pay_order_id);
    setTimeout(() => {

    }, 200);
  },
  methods: {
    load_pay_order: function (pay_order_id) {

      this.try_count += 1;
      if(this.try_count > 10){
        return;
      }

      this.$axios.get("/app/pay/order", {
        params: {
          'id': pay_order_id,
        }
      }).then((response) => {
        console.log("response:", response);
        this.pay_order = response.data.pay_order;
        this.create_qrcode();
        if (!this.pay_order) {
          setTimeout(this.load_pay_order, 100);
        }
      }).catch((e) => {
        if (!this.pay_order) {
          setTimeout(this.load_pay_order, 100);
        }
      })
    },
    create_qrcode: function () {
      this.created_qr = new QRCode(document.getElementById("qrcode"), {
        text: this.pay_order.redirect_url,
        width: 256,
        height: 256,
        colorDark: "#000000",
        colorLight: "#ffffff",
        correctLevel: QRCode.CorrectLevel.M
      });
    }
  }
}
